import axios from 'axios';
import {Link} from 'react-router-dom'
import {
  useMediaQuery,
  Button,
  Avatar,
  Typography,
  Grid,
  Divider,
  Box,
} from '@material-ui/core';
import { Image } from 'components/atoms';
import { CardProduct, SectionAlternate } from 'components/organisms';
import CreditCardIcon from '@material-ui/icons/CreditCard';
const useStyles = makeStyles(theme => ({
  pagePaddingTop: {
    padding: theme.spacing(3),
    paddingBottom: theme.spacing(3),
    [theme.breakpoints.up('md')]: {
      paddingTop: theme.spacing(5),
      paddingBottom: theme.spacing(5),
    },
  },
  sectionAlternate: {
    '& .section-alternate__content': {
      padding: theme.spacing(3),
      paddingBottom: theme.spacing(3),
      [theme.breakpoints.up('md')]: {
        paddingTop: theme.spacing(5),
        paddingBottom: theme.spacing(5),
      },
    },
  },
  searchInputContainer: {
    background: theme.palette.alternate.main,
    padding: theme.spacing(2),
    boxShadow: '0 4px 14px 0 rgba(0, 0, 0, 0.11)',
    borderRadius: theme.spacing(1),
    width: '100%',
    height: '100%',
    display: 'flex',
    alignItems: 'center',
    '& .MuiOutlinedInput-notchedOutline': {
      border: '0 !important',
    },
    '& .MuiInputAdornment-positionStart': {
      marginRight: theme.spacing(2),
    },
    '& .MuiOutlinedInput-adornedStart': {
      paddingLeft: 0,
    },
    '& .MuiOutlinedInput-input': {
      padding: 0,
    },
    [theme.breakpoints.down('sm')]: {
      padding: theme.spacing(1),
    },
  },
  searchButton: {
    maxHeight: 45,
    minWidth: 135,
    [theme.breakpoints.down('sm')]: {
      minWidth: 'auto',
    },
  },
  cardProduct: {
    display: 'flex',
    flexDirection: 'column',
    height: '100%',
    borderRadius: theme.spacing(1),
    '& .card-product__content': {
      paddingTop: theme.spacing(2),
      paddingBottom: theme.spacing(2),
    },
  },
  image: {
    objectFit: 'cover',
    borderRadius: theme.spacing(0, 0, 20, 0),
  },
  blogContent: {
    display: 'flex',
    flexDirection: 'column',
    height: '100%',
  },
  list: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
  avatarContainer: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
  avatar: {
    marginRight: theme.spacing(1),
  },
  divider: {
    marginBottom: theme.spacing(1),
  },
  button: {
    minWidth: '100%',
    maxWidth: '100%',
    [theme.breakpoints.up('sm')]: {
      minWidth: 420,
    },
  },
  fontWeightBold: {
    fontWeight: 'bold',
  },
  answerCount: {
    padding: theme.spacing(1 / 2, 1),
    borderRadius: theme.spacing(1),
    background: theme.palette.secondary.light,
    color: 'white',
    fontWeight: 300,
  },
  priceCta: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
  categoryIconButton: {
    display: 'flex',
    width: '100%',
    justifyContent: 'flex-end',
  },
  icon:{
    fontSize:"40px",
    "&:before":{
      content: "\f00d",
    }
  },
  tag: {
    padding: theme.spacing(1 / 2, 1),
    color:'white',
    border: `1px solid ${theme.palette.secondary.light}`,
    fontWeight: 600,
    borderRadius: theme.spacing(1),
    background: `${theme.palette.secondary.light}`,
    margin: theme.spacing(0, 1, 1, 0),
    cursor: 'pointer',
    [theme.breakpoints.up('md')]: {
      margin: theme.spacing(0, 2, 2, 0),
      padding: theme.spacing(1, 2),
    },
    '&:hover': {
      color: `${theme.palette.secondary.main}`,
      background: 'white',
    },
  },
  tagClicked:{
    color: `${theme.palette.secondary.main}`,
    background: 'white',
    padding: theme.spacing(1 / 2, 1),
    border: `1px solid ${theme.palette.secondary.light}`,
    fontWeight: 600,
    borderRadius: theme.spacing(1),
    margin: theme.spacing(0, 1, 1, 0),
    cursor: 'pointer',
    [theme.breakpoints.up('md')]: {
      margin: theme.spacing(0, 2, 2, 0),
      padding: theme.spacing(1, 2),
    },
  },
}));

const Result = props => {
  const {tags,className, ...rest } = props;
  const classes = useStyles();
  const [productsloaded,setProductsLoaded]=useState(6)
  const [tagClicked,setTagClicked]=useState(false)
  const [data,setData]=useState([])
  const [category,setCategory]=useState(window.location.pathname.substring(window.location.pathname.lastIndexOf('/'),window.location.pathname.length).replaceAll('%20',' '))
  useEffect(() => {
    axios.get('http://localhost:8080/products'+category)
    .then(response => {
      setData(response.data);
      //console.log("ioi",response.data.rows)
    })
    .catch((error) => {
      console.log(error);
    })

},[tagClicked]);
  const theme = useTheme();
  const isMd = useMediaQuery(theme.breakpoints.up('md'), {
    defaultMatches: true,
  });
  const loadMore =()=>{
    if(productsloaded<data.length){
      setProductsLoaded(productsloaded+6)
    }
  }
  const clickTagHandler=(item)=>{
    
    setCategory(item)
    setTagClicked(!tagClicked)
    //window.location='/products'+item
    window.history.pushState('', '', '/products'+item)

  }
  const BlogMediaContent = props => (
    <Image
      src={props.picture_url__c}
      className={classes.image}
      lazyProps={{ width: '100%', height: '250px' }}
    />
  );

  const BlogContent = props => (
    <div className={classes.blogContent}>
      
      <Typography variant="h6" color="textPrimary" gutterBottom>
        {props.title}
      </Typography>
      <Box overflow="hidden"
        textOverflow="ellipsis"
       >
      <Typography variant="body1" color="textSecondary" >
        {props.subtitle.substring(0,120)}......
      </Typography>
      </Box>
      <div style={{ flexGrow: 1 }} />
      <div className={classes.priceCta} style={{border:'0px solid black'}}>
                  <Typography
                    color="secondary"
                    variant="h5"
                    className={classes.fontWeightBold}
                  >
                    ${props.price}
                    

                  </Typography>
                  <Box>
                        <Button color="primary">
                          <CreditCardIcon/>
                        </Button>
                        <Button onClick={()=>localStorage.setItem("family",props.family)}>
                       <Link to={'/products/product/'+props.sfid}>
                       <ArrowRightAltIcon
                          className={classes.icon}
                          color={'secondary'}  
                        />
                       </Link>
                    
                    </Button>
                  </Box>
                </div>
      
      <Divider className={classes.divider} />
      <div className={classes.list}>
        <div className={classes.avatarContainer}>
          <Avatar  src={props.fullphotourl}  className={classes.avatar} />
          <Typography variant="body2" color="textPrimary">
            {props.author}
          </Typography>
        </div>
        <Typography variant="overline" color="textSecondary">
          {(new Date(props.date)).getDay()+"/"+(new Date(props.date)).getMonth()+"/"+(new Date(props.date)).getFullYear()}
        </Typography>
      </div>
    </div>
  );

  return (
    <div className={className} {...rest}>
      <SectionAlternate className={classes.sectionAlternate}>
      <Grid container spacing={isMd ? 4 : 2}>
        {
          isMd && 
          
          <Grid  item xs={12}  container >
            <Box
                sx={{
                  display: 'flex',
                  flexWrap: 'wrap',
                  width:'100%',
                  borderRadius: 1,
                }}
                justifyContent={isMd ?'center':'flex-start'}
              >
          
              {tags.map((item, index) => (
                <Box  m={{xs:0.5,md:1}}  key={index} onClick={()=>clickTagHandler("/"+item.family)} > 
                <Typography
                variant="body2"
                color="primary"
                className={"/"+item.family===category ?classes.tagClicked:classes.tag}
               
              >
                {item.family}
              </Typography>
              </Box>
              ))}
             </Box>
          </Grid>
        }
         
          <Grid item xs={12} container>
            <Typography variant="body1" className={classes.answerCount}>
              {data.length} Result Found
            </Typography>
          </Grid>
          {data.map((item, index) => (
              <Grid item xs={12} sm={6} key={index} md={4} data-aos="fade-up">
              {
                 index <productsloaded &&
                 <CardProduct
                withShadow
                liftUp
                className={classes.cardProduct}
                mediaContent={
                  <BlogMediaContent {...item} alt={item.name} />
                }
                cardContent={
                  <BlogContent
                    title={item.name}
                    subtitle={item.description}
                    author={item.username}
                    date={item.createddate}
                    fullphotourl={item.mediumphotourl}
                    price={item.unitprice}
                    sfid={item.pbesfid}
                    family={item.family}
                  />
                }
              />
              }
            </Grid>
            
          ))}
          <Grid item xs={12} container justifyContent="center">
            <Button
              variant="contained"
              color="primary"
              size="large"
              onClick={()=>loadMore()}
              className={classes.button}
            >
              Load more
            </Button>
          </Grid>
        </Grid>
      </SectionAlternate>
    </div>
  );
};

Result.propTypes = {
 
  /**
   * data to be rendered
   */
  //data1: PropTypes.array.isRequired,
  tags: PropTypes.array.isRequired,

  
};

export default Result;
